.masterpass {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.mnemonic {
  background: #999;
  width: 75px;
  height: calc(12pt + 1em);
  border: 1px solid #999;
  border-radius: 4px;
  box-shadow: inset 0 0.0625em 0.125em rgb(10 10 10 / 5%);
}

.exceptions {
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  height: 185px;
}

.exception-card {
  background: #ccc;
  padding: 10px;
  padding-bottom: 15px;
  border-radius: 2px;
  display: inline-block;
  margin-right: 5px;
  box-shadow:0 2px 5px rgba(0, 0, 0, 0.3);
}

.exception-add {
  width: 100%;
  cursor: pointer;
}
.exception-del {
  cursor: pointer;
}
